<template>
  <div class="container">
    <img class="wc-preview-img custom-img" :src="url" v-for="(url, key) in list" :key="key" @click="$preview($event, list, key)">
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      list: [
        /*
        'http://img4.imgtn.bdimg.com/it/u=2245363424,2283929512&fm=214&gp=0.jpg',
        'http://img.juimg.com/tuku/yulantu/140331/330500-1403310Z44746.jpg',
        'http://img.taopic.com/uploads/allimg/140714/240451-140G4063Z667.jpg',
        'http://img.taopic.com/uploads/allimg/120217/10025-12021GZ94780.jpg',
        'http://i2.sanwen.net/doc/1608/704-160P9223953.jpg',
        'http://pic.58pic.com/58pic/15/36/26/12J58PICuz4_1024.jpg',
        'http://img.taopic.com/uploads/allimg/140319/234725-14031ZTP876.jpg',
        'http://www.taopic.com/uploads/allimg/120105/1942-12010511310769.jpg'
        */
      ]
    }
  },
  methods: {
    getQueryString: function (name) {
      var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)') // 匹配目标参数
      var result = window.location.search.substr(1).match(reg) // 对querystring匹配目标参数
      if (result != null) {
        return decodeURIComponent(result[2])
      } else {
        return null
      }
    }
  },
  created: function () {
    var urll = this.getQueryString('url')
    if (urll) {
      let se = prompt('jjs://HTTP_GET_IMAGE_LIST#' + urll)
      if (se && se !== 'OK') {
        try {
          this.list = JSON.parse(se)
        } catch (e) {}
      }
    }
  }
}
</script>

<style>
html {
  background-color: #000;
}
.container {
  text-align: center;
  background-color: #000;
}
.custom-img {
  max-width: 100%;
}
</style>



